import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniPushService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-push.service';
import { CheckAppService } from 'src/app/commonSRC/psServices/checkApp/checkApp.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsKeyinInput, IOptionsPsTemplateForm } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from '../../commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'device-details',
  templateUrl: './device-details.page.html',
})
export class DeviceDetailsPage extends OmniBasePage implements OnInit {
  private formGroup = new FormGroup({});
  private authenticationResponse;
  public deviceDetailsVO = {};
  private authenticationResponseToken = null;

  public formOptions: IOptionsPsTemplateForm = {
    group: this.formGroup,
    submitOptions: {
      extraParams: {
        status: 'A',
        userName: this.navService.getAllParams().userName,
        ocUserId: this.navService.getAllParams().ocUserId
      },
      group: this.formGroup,
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.submitDeviceAction,
      postCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.postLogin(response));
          });
        },
        params: [this],
        executionClass: this
      },
    },
    requestObject: this.deviceDetailsVO,
  };

  public deviceModelOptions: IOptionsPsKeyinInput = {
    group: this.formGroup,
    fcName: 'deviceModel',
    labelKey: 'device_model_key'
  };
  public devicePlatformOptions: IOptionsPsKeyinInput = {
    group: this.formGroup,
    fcName: 'devicePlatform',
    labelKey: 'device_platform_key'
  };
  public deviceVersionOptions: IOptionsPsKeyinInput = {
    group: this.formGroup,
    fcName: 'deviceVersion',
    labelKey: 'device_version_key'
  };
  public userNameOptions: IOptionsPsKeyinInput = {
    group: this.formGroup,
    fcName: 'userName',
    labelKey: 'device_version_key'
  };
  constructor(private navService: PsNavigatorService, public commonProv: PsCommonService,
    public loggerP: LoggerService, private omniPush: OmniPushService,
    private checkAppService: CheckAppService) {
    super();
  }

  async ngOnInit() {
    super.init();
    this.baseFormGroup = this.formGroup; // Added by Zunair For #BUG 1257538
    this.authenticationResponse = this.navService.getAllParams() ? this.navService.getAllParams() : null;
    let deviceDetails = await this.checkAppService.returnDeviceDetails();
    deviceDetails['userName'] = this.navService.getAllParams()?.userName;
    if (deviceDetails) {
      this.commonProv.copyObject(this.deviceDetailsVO, deviceDetails, false, true);

    }

    //nabil feghali, check if authenticationToken is already returned from authenticateUser webservice and save it. 
    // it will be used after calling the submit action button used to register the device, and will be passed to loginUser webservice
    const authToken = this.commonProv.session.getValueOf(ConstantCommon.SERVER_AUTH_TOKEN);
    if (authToken) {
      this.authenticationResponseToken = authToken;
    }
  }
  postLogin(response) {
    if (response.outputCode == -1) {
      CommonUtils.presentFailureAlert(CommonUtils.translate('something_went_wrong_key'));
    } else {
      this.commonProv.presentLoading();
      const routesArray = this.navService.preLoginRoutingChecking(this.authenticationResponse);
      if (routesArray.length === 0) {
        this.login(this.authenticationResponse.ocUserId, this.authenticationResponse.userName);
      }
    }
  }
  login(ocUserId, userName) {

    //nabil feghali, reset the authentication token in session after trusting the device, the authentication token will be used in loginUser service
    if (this.authenticationResponseToken) {
      this.commonProv.session.append(ConstantCommon.SERVER_AUTH_TOKEN, this.authenticationResponseToken);
    }

    const loginRequest = { ocUserId: ocUserId, userName: userName }; // no additional information to be passsed to the login service (CompCode, appId, channelId,userId are sent via the interceptor)
    this.omniPush.login(loginRequest).then();
  }
}
